
.image-slider-container{
    animation-timing-function: ease-in;
    animation-duration: 2s;
    align-content: center;
    z-index: 999;
    overflow: hidden;
    background: #6b6989;

    margin: 0px auto;
    display: inline-block;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    max-width: calc(100%);
}

.image-slider{
  animation-timing-function: ease-in;
  animation-duration: 2s;
  align-content: center;
  z-index: 999;
  overflow: hidden;
  background: #6b6989;

  margin: 0px auto;
  display: inline-block;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  max-width: calc(100vh - 15%);

}

.image-slider-container div{
  overflow: hidden;
}

.close-btn{
    display: block;
    position: fixed;
    top: 0;
    right: 0;
    width: 40px;
    height: 40px;
    font-size: x-large;
    margin: 7px;
    z-index: 99999;
    cursor: pointer;
}