.sidebar.large{
    width: 100%;
}

.sidebar-container:has(.sidebar.open){
  width: 100%;
  position: fixed;
  z-index: 9999;
  height: 100%;
}


.sidebar.open{
    display: grid;
      animation-timing-function: ease-in;
      animation-duration: 2s;
      width: 50%;
      align-content: center;
      position: fixed;
      z-index: 9999;
      height: 100%;
      background: #323054;
  }
  
  .sidebar{
    display: none;
    animation-timing-function: ease-in;
    animation-duration: 2s;
    width: 0;
  }

  .menus{
    display: contents;
  }

  

.bar-item{
    font-size: xx-large;
    color: aliceblue;
    padding-bottom: 18px;
  }
  
  .menu{
    display: block;
    position: fixed;
    top: 0;
    width: 40px;
    height: 40px;
    font-size: x-large;
    margin: 7px;
    z-index: 99999;
    cursor: pointer;
  }
  