.App.ui-block .sidebar-container {
    display: none;
}

.App.ui-block .close-btn{
    display: none;
}

.loading-container{
    display: flex;
    flex-direction: column;
    align-content: center;
    flex-wrap: wrap;
    transition: cubic-bezier(0.175, 0.885, 0.32, 1.275);
    animation-timing-function: ease-in;
    animation-duration: 10s;
}